@import url(https://fonts.googleapis.com/css2?family=Explora&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Bonheur+Royale&family=Shadows+Into+Light&display=swap);
body {
    background-color: #ededed;
    background: linear-gradient(150deg, rgba(250,250,250,1) 0%, rgba(255,255,255,1) 50%, rgba(250,250,250,1) 100%);
}
.bg-dark {
    width: -webkit-fit-content;
    width: -moz-fit-content;
    width: fit-content;
    background: rgb(221,221,211);
    background: linear-gradient(150deg, rgba(250,250,250,1) 0%, rgba(255,255,255,1) 50%, rgba(250,250,250,1) 100%);
    color: #000;
    margin: 0 auto;
}

.container {
    width: 96vw;
    background: linear-gradient(150deg, rgba(240,240,240,1) 0%, rgba(255,255,255,1) 50%, rgba(240,240,240,1) 100%);
    color: #000;
    margin: 0 auto;
}

.hvr-lght {
    text-decoration: none !important;
    transition: all .3s ease-in;
    cursor: pointer;
}


.hvr-lght:hover {
    
    background-color: #fff1ff;
    border-radius: 15px;
    border: 1px solid #000;
}

.hvr-lght--regular {
    text-decoration: none !important;
    transition: all .3s ease-in;
    cursor: pointer;
}


.hvr-lght--regulart:hover {
    background-color: #fff1ff;
}


.hvr--default {
    cursor: pointer;
}

.apiPost {
    display: flex; 
    justify-content: space-between;
    align-items: center;
    font-family: 'Shadows Into Light', cursive;
    font-size: 20px;
    background-color: #eee;
    padding-left: 20px;
}

.apiPost + .apiPost {
    margin-top: 15px;
}

.apiPost:hover {
    background-color: #fff;
}

.link-slf {
    text-decoration: none;
    color: #222;
}

.link-slf:hover {
    text-decoration: none;
    color: #600;
}

.link-slf:visited{
    color: inherit;
}

.link--inactive {
    pointer-events: none;
    opacity: .7;
}

.font--explora {
    font-weight: 700;
    font-size: 46px;
    font-family: 'Explora', cursive;
}

.li-citation + .li-citation {
    margin-top: 3vh;
}

